import {Tabs, Tab, TabList, TabPanel} from "react-tabs";
import {useEffect, useState} from "react";
import {Link} from "react-router-dom";
import FooterMenu from "./FooterMenu";
import axios from "axios";

const Home = () => {
    const [categories, setCategories] = useState([])
    const [projects, setProjects] = useState([])
    const [about,setAbout] = useState({})
    useEffect(() => {
        window.scrollTo(0, 0)
        //get categories
        axios({
            method:'get',
            url:process.env.REACT_APP_API_URL + 'projects/categories',
            headers:{
                Authorization:'Bearer ' + process.env.REACT_APP_API_TOKEN
            }
        })
            .then(response => {
                setCategories(response.data)
            })
            .catch(error => {
                console.log(error)
            })
        //get projects
        axios({
            method:'get',
            url:process.env.REACT_APP_API_URL + 'projects',
            headers:{
                Authorization:'Bearer ' + process.env.REACT_APP_API_TOKEN
            }
        })
            .then(response => {
                setProjects(response.data)
            })
            .catch(error => {
                console.log(error)
            })
        //get about
        axios({
            method:'get',
            url:process.env.REACT_APP_API_URL + 'about',
            headers:{
                Authorization:'Bearer ' + process.env.REACT_APP_API_TOKEN
            }
        }).then(response => {
            setAbout(response.data)
        })
            .catch(error => {
                console.log(error)
            })
    }, [])
    return (
        <>
            <div className="container">
                <div className="content">
                    <div className="info">
                        <p dangerouslySetInnerHTML={{__html:about.name}}></p>
                        <small dangerouslySetInnerHTML={{__html:about.degree}}></small>
                    </div>
                    <div className="about">
                        <div className="about-title">
                            About
                        </div>
                        <div className="about-body"  dangerouslySetInnerHTML={{__html:about.body}}></div>
                    </div>
                </div>
            </div>
            <div className="projects">
                <Tabs>
                    <TabList>
                        <Tab>All</Tab>
                        {categories.map((category, index) => (
                            <Tab key={index}>{category.name}</Tab>
                        ))}
                    </TabList>
                    <div className="projects-content" >
                        <TabPanel>
                            {projects.map((item) => (
                                <div key={item.id} className="project">
                                    <Link to={`/project/${item.id}`}>
                                        <img src={process.env.REACT_APP_SITE + item.media[0].media} alt={item.title}/>
                                        <div className="title">{item.title}</div>
                                    </Link>
                                </div>
                            ))}
                        </TabPanel>
                        {categories.map((item) => (
                            <TabPanel key={'panel' + item.id}>
                                {projects.filter(p => p.categories[0].id === item.id).map((item2) => (
                                    <div key={item.id} className="project">
                                        <Link to={`/project/${item2.id}`}>
                                            <img src={process.env.REACT_APP_SITE + item2.media[0].media} alt={item2.title}/>
                                            <div className="title">{item.title}</div>
                                        </Link>
                                    </div>
                                ))}
                            </TabPanel>
                        ))}
                    </div>
                </Tabs>
                <div className="read-more">Read <span>more</span></div>
            </div>
        </>

    )
}
export default Home
