import {Tab,Tabs,TabList, TabPanel} from "react-tabs";
import {useEffect, useState} from "react";
import axios from "axios";

const Blogs = () => {
    const [categories,setCategories] = useState([])
    useEffect(() => {
        axios({
            method:'get',
            url:process.env.REACT_APP_API_URL + 'blogs/categories',
            headers:{
                Authorization:'Bearer ' + process.env.REACT_APP_API_TOKEN
            }
        })
            .then(resonse => {
                setCategories(resonse.data)
            })
            .catch(error => {
                console.log(error)
            })
    })
    return(
        <div className="container">
            <Tabs className="blogs">
                <TabList className="blogs-tab-list">
                    {categories.map(item => (
                        <Tab className="blogs-tab-list-tab">{item.name}</Tab>
                    ))}
                </TabList>
                <TabPanel className="blogs-tab-panel">asdf</TabPanel>
                <TabPanel>asasdfdfdf</TabPanel>
            </Tabs>
        </div>
    )
}
export default Blogs
